import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { Location } from '@angular/common';
import { UtilsService } from '../@core/utils.service';
import { BasePage } from '../base/base.page';
import { Constants } from '../@global/constants';
import { AuthState } from '../@store/auth/auth.state';
import { SignOut } from '../@store/auth/auth.actions';
import { AnalyticsService } from '../@core/analytics.service';
import { GoogleAnalyticsService } from '../@core/google-analytics.service';
import { NavigationExtrasService } from '../@core/navigation-extras.service';
import { PushService } from '../@core/push.service';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.page.html',
  styleUrls: ['./settings.page.scss'],
})
export class SettingsPage extends BasePage implements OnInit {
  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    private analyticsService: AnalyticsService,
    private navigationExtrasService: NavigationExtrasService,
    private googleAnalyticsService: GoogleAnalyticsService,
    private pushService: PushService
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {}

  public signOut() {
    // pinpoint
    this.analyticsService.record(Constants.USER_LOGGED_OUT_EVENT, {
      userId: this.store.selectSnapshot(AuthState.getUserId),
    });

    // google analytics
    this.googleAnalyticsService.userSignOut();

    // turn off push notifications
    this.pushService.setExternalUserId('null');

    this.store.dispatch(new SignOut());
    this.navigationExtrasService.setExtras({ showGoBack: false });
    this.router.navigate(['/login']);
  }
}
